body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.board, .controls {
  width: 400px;
  margin: 25px auto;
}

.left, .right {
  flex-grow: 1;
}

.rank {
  display: flex;
  flex-direction: row;
}

.space {
  width: 50px;
  height: 50px;
  transition: background-color 0.2s ease;
}

.space.white {
  background: #DEE3E6;
}

.space.black {
  background: #8BA2AE;
}

.space.green {
  background: #56FF65;
}

.space.red {
  background: #FF6B56;
}

.instruction {
  text-align: center;
  font-size: 4rem;
  font-weight: 900;
  text-transform: uppercase;
  color: #444;
}

.results {
  margin-top: 100px;
  width: 100%;
}

.result {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

